<template>
  <div class="index">
    <HeaderTop></HeaderTop>

    <div class="flex flex-direction align-center justify-center">
      <img style="margin-top: 70px;" src="../assets/img/logo.png" alt="">
      <p style="color: #fff;font-weight: 700;font-size: 31px;margin-bottom: 20px;">CARBON LABS</p>
      <p style="color: #868C91;font-size: 18px;">a community driven</p>
      <p style="color: #868C91;font-size: 18px;">fair launched DeFi pilot project</p>
    </div>

    <div class="indexdata">
      <div class="flex">
        <div class="flex_sub">
          <p>
            <animated-number :value="price" :formatValue="formatToPrice" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.newprice')}}/USDT</p>
        </div>
        <div class="flex_sub">
          <p>
            <animated-number :value="supply" :formatValue="MillenniumRepresentation" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.Totalissue')}}</p>
        </div>
        <div class="flex_sub">
          <p>
            <animated-number :value="holders" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.holder')}}</p>
        </div>
      </div>
      <div class="flex">
        <div class="flex_sub">
          <p>
            <animated-number :value="price" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.Totalamount')}}/USDT</p>
        </div>
        <div class="flex_sub">
          <p>
            <animated-number :value="market" :formatValue="MillenniumRepresentation" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.market')}}/USDT</p>
        </div>
        <div class="flex_sub">
          <p>
            <animated-number :value="totalburn" :duration="300" />
          </p>
          <p class="tips">{{$t('lang.burning')}}</p>
        </div>
      </div>
    </div>

    <div class="info">
      <span>{{$t('lang.info')}}</span>
    </div>

    <!-- 捐赠空投奖励 -->
    <div class="speed">
      <div class="flex align-center justify-between">
        <span>{{$t('lang.di')}}{{period}}{{$t('lang.percent')}}：</span><span>{{(totalAirdop-restAirdop) | numString}}/{{totalAirdop}}
        </span>
      </div>
      <div class="line_bg">
        <div class="line" :style="{width:percentage}">{{percentage}}</div>
      </div>
    </div>

    <!-- 确认捐赠 -->
    <div class="donation">
      <div class="input flex justify-between align-center">
        <span>{{$t('lang.donations')}}</span>
        <input type="number" v-model="donate_number" placeholder="0.0000">
        <span>USDT</span>
      </div>

      <div class="flex justify-between">
        <p style="font-size: 10px;margin-top: 10px;">
          <span>{{$t('lang.authorized')}}：</span>
          <span style="font-size: 16px;">{{allowancebalance | numString}}</span>
          <span>USDT</span>
        </p>
        <p style="text-align: right;font-size: 10px;margin-top: 10px;">
          <span>{{$t('lang.Walletbalance')}}：</span>
          <span style="font-size: 16px;">{{usdtbalance | numString}}</span>
          <span>USDT</span>
        </p>
      </div>

      <div class="flex justify-between">
        <el-button class="button" type="primary" :loading="loading" :disabled="disabled" @click="approve()">{{text}}
        </el-button>
        <el-button class="button" type="primary" :loading="loading2" :disabled="disabled2" @click="donate()">
          {{donatetext}}
        </el-button>
      </div>



    </div>

    <!-- 介绍 -->
    <div class="carbon">
      <p style="font-size: 14px;color: #fff;">Carbon Labs</p>
      <p style="padding: 30px 0;">{{$t('lang.carbon')}}</p>
      <div class="list">
        <div class="list_box">
          <img src="../assets/img/i1.png" alt="">
          <p class="title">{{$t('lang.t1')}}</p>
          <div class="infor">
            <p>{{$t('lang.t2')}}</p>
            <p>{{$t('lang.t3')}}</p>
          </div>
        </div>

        <div class="list_box">
          <img src="../assets/img/i2.png" alt="">
          <p class="title">{{$t('lang.t4')}}</p>
          <div class="infor">
            <p>{{$t('lang.t5')}}</p>
            <p>{{$t('lang.t6')}}</p>
          </div>
        </div>

        <div class="list_box">
          <img src="../assets/img/i3.png" alt="">
          <p class="title">{{$t('lang.t7')}}</p>
          <div class="infor">
            <p>{{$t('lang.t8')}}</p>
            <p>{{$t('lang.t9')}}</p>
          </div>
        </div>

        <div class="list_box">
          <img src="../assets/img/i4.png" alt="">
          <p class="title">{{$t('lang.t10')}}</p>
          <div class="infor">
            <p>{{$t('lang.t11')}}</p>
            <p>{{$t('lang.t12')}}</p>
          </div>
        </div>

        <div class="list_box">
          <img src="../assets/img/i5.png" alt="">
          <p class="title">{{$t('lang.t13')}}</p>
          <div class="infor">
            <p>{{$t('lang.t14')}}</p>
            <p>{{$t('lang.t15')}}</p>
          </div>
        </div>

        <div class="list_box">
          <img src="../assets/img/i6.png" alt="">
          <p class="title">{{$t('lang.t16')}}</p>
          <div class="infor">
            <p>{{$t('lang.t17')}}</p>
            <p>{{$t('lang.t18')}}</p>
          </div>
        </div>

      </div>

    </div>

    <!-- 邀请 -->
    <div class="invite_modal" v-if="invite_modal">
      <div>
        <p style="text-align: center;margin-bottom: 15px;font-size: 18px;">{{$t('lang.y1')}}</p>
        <p style="font-size: 10px;margin-bottom: 15px;font-size: 12px;">{{$t('lang.y2')}}</p>
        <div class="input flex justify-between align-center">
          <input type="text" v-model="invite_address" :placeholder="$t('lang.bsc')">
        </div>
        <el-button class="button" type="primary" :loading="loading3" @click="setInvitors()">{{$t('lang.conf')}}
        </el-button>
        <p style="text-align: center;margin-top: 15px;font-size: 11px;">{{$t('lang.y3')}}</p>
      </div>
    </div>

  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  import AnimatedNumber from "animated-number-vue";
  import {
    GetMoney
  } from './api/user'
  export default {
    components: {
      HeaderTop,
      AnimatedNumber
    },
    name: 'index',
    data() {
      return {
        cblContract: new this.Web3.eth.Contract(this.$store.state.cbl_abi, this.$store.state.cbl_address),
        usdtContract: new this.Web3.eth.Contract(this.$store.state.usdt_abi, this.$store.state.usdt_address),
        donateContract: new this.Web3.eth.Contract(this.$store.state.donate_abi, this.$store.state.donate_address),
        address: '', //钱包地址
        invite_address: '', //邀请者地址
        loading: false, //按钮禁用
        disabled: true,
        loading2: false, //按钮禁用
        disabled2: true,
        loading3: false,
        donate_number: '', //捐赠数量
        invite_modal: false, //邀请弹窗
        allowancebalance: '', //授权额度
        text: this.$t('lang.text'),
        donatetext: this.$t('lang.donatetext'),
        usdtbalance: '', //usdt余额
        totalburn: '', //已燃烧
        supply: '', //发行总量
        market:'',//市值
        amount:'0',//质押总额

        totalAirdop: '', //捐赠总量
        restAirdop: '', //剩余捐赠数量
        holders:'0',//持有人

        title: this.$t('lang.title'),
        price: '0', //
        percentage: '', //捐赠空投奖励进度
        period: '', //空投期数



      }
    },

    mounted: function() {
      this.invite_address = this.$route.params.share + ''

      this.address = ethereum.selectedAddress
      console.log('cblContract', this.cblContract.methods)
      console.log('usdtContract', this.usdtContract.methods)
      console.log('donateContract', this.donateContract.methods)

      this.getData();
      this.donateData();


    },

    methods: {
      golink: function(link) {
        this.$router.push(link)
      },
      getData: function() {
        this.address = ethereum.selectedAddress
        //已燃烧
        this.cblContract.methods
          .totalBurn()
          .call()
          .then(res => {
            this.totalburn = Number(this.Web3.utils.fromWei(res, 'szabo')).toFixed(0);
            console.log('已燃烧', this.totalburn)
          })
        //发行总量
        this.cblContract.methods
          .totalSupply()
          .call()
          .then(res => {
            this.supply = Number(this.Web3.utils.fromWei(res, 'szabo')).toFixed(0);
            console.log('发行总量', this.supply)
            //最新价格
            this.cblContract.methods
              .getLastPriceUSDT()
              .call()
              .then(res => {
                this.price = this.Web3.utils.fromWei(res, 'ether')
                console.log('最新价格', this.Web3.utils.fromWei(res, 'ether'))
                let num = this.price*this.supply
                this.market = Number(num).toFixed(0);
              })
          })


      },
      //捐赠空投数据
      donateData: function() {
        // 捐赠总量
        this.donateContract.methods
          .totalAirdopAmount()
          .call()
          .then(res => {
            this.totalAirdop = this.Web3.utils.fromWei(res, 'szabo')
            console.log('捐赠总量', this.totalAirdop)
            // 剩余捐赠总量
            this.donateContract.methods
              .restAirdopAmount()
              .call()
              .then(res => {
                this.restAirdop = this.Web3.utils.fromWei(res, 'szabo')
                this.percentage = ((this.totalAirdop - this.restAirdop) / this.totalAirdop) * 100;
                this.percentage = Number(this.percentage).toFixed(2) + '%';
                console.log('剩余捐赠总量', this.Web3.utils.fromWei(res, 'szabo'))
              })
          })

        this.donateContract.methods
          .period()
          .call()
          .then(res => {
            this.period = res
            console.log('空投期数', res)
          })

      },
      // 捐赠
      donate: function() {
        if (!this.donate_number) {
          this.$message({
            message: this.$t('lang.place'),
            type: 'warning'
          });
          return
        }
        if(this.allowancebalance<this.donate_number){
          this.$message({
            message: this.$t('lang.place6'),
            type: 'warning'
          });
          return
        }
        this.loading2 = true
        this.donatetext = this.$t('lang.place1')
        this.donateContract.methods
          .Donate(this.Web3.utils.toWei(this.donate_number, 'ether'))
          .send({
            from: this.address
          })
          .then(res => {
            console.log('捐赠', res)
            this.loading2 = false
            this.donate_number = '';
            this.donatetext = this.$t('lang.place2')
            this.allowance();
            this.donateData();
            this.$message({
              message: this.$t('lang.place3'),
              type: 'success'
            });
          }).catch(error => {
            this.loading2 = false
            this.donate_number = '';
            this.donatetext = this.$t('lang.place2')
            console.error(error)
          })
      },
      // 授权额度查询
      allowance: function() {
        this.address = ethereum.selectedAddress
        this.usdtContract.methods
          .allowance(this.address, this.$store.state.donate_address)
          .call()
          .then(res => {
            this.allowancebalance = this.Web3.utils.fromWei(res, 'ether');
            if (res > 0) {
              this.disabled = true
              this.disabled2 = false
              this.loading = false
            } else {
              this.disabled = false
              this.disabled2 = true
            }
            this.text = this.$t('lang.text')
            this.donatetext = this.$t('lang.donatetext')
            console.log('授权额度', this.Web3.utils.fromWei(res, 'ether'))
          })
        //获取usdt余额
        this.usdtContract.methods
          .balanceOf(this.address)
          .call()
          .then(res => {
            this.usdtbalance = this.Web3.utils.fromWei(res, 'ether')
            console.log('usdt余额', res)
          })
      },
      //授权
      approve: function() {
        if (!this.donate_number) {
          this.$message({
            message: this.$t('lang.place'),
            type: 'warning'
          });
          return
        }
        this.loading = true
        this.text = this.$t('lang.place4')
        this.usdtContract.methods
          .approve(this.$store.state.donate_address, this.Web3.utils.toWei(this.donate_number, 'ether'))
          .send({
            from: this.address
          })
          .then(res => {
            console.log('授权', res)
            this.text = this.$t('lang.place5')
            this.allowance()
            this.$message({
              message: this.$t('lang.place5'),
              type: 'success'
            });
          })
      },
      // 获取邀请者
      getInvitors: function() {
        this.address = ethereum.selectedAddress
        this.cblContract.methods
          .isAction(this.address)
          .call()
          .then(res => {
            console.log('邀请者', res)
            if (!res) {
              this.invite_modal = true
              if (this.invite_address == 'undefined') {
                this.invite_address = ''
              }
            } else {
              this.invite_modal = false
            }

          })
      },
      // 绑定邀请关系
      setInvitors: function() {
        if (!this.Web3.utils.isAddress(this.invite_address) && this.invite_address != '') {
          this.$message({
            message: this.$t('lang.bsc2'),
            type: 'warning'
          });
          return
        }
        if (this.invite_address == '') {
          this.invite_address = '0x0000000000000000000000000000000000000000'
        }

        this.loading3 = true
        this.cblContract.methods
          .setInvitor(this.invite_address)
          .send({
            from: this.address
          })
          .then(res => {
            console.log('绑定邀请关系', res)
            this.loading3 = false
            this.$message({
              message: this.$t('lang.y4'),
              type: 'success'
            });
            this.getInvitors();
          }).catch(error => {
            this.loading3 = false
            if (error.code === 4001) {
              // this.invite_modal = false
            } else {
              console.error(error)
            }
          })
      },

      //数字转换为千分位
      formatToPrice(s) {
        s = s + ''
        s = s.replace(/^(\d*)$/, "$1.");
        s = (s + "0000").replace(/(\d*\.\d\d\d\d)\d*/, "$1");
        s = s.replace(".", ",");
        var re = /(\d)(\d{3},)/;
        while (re.test(s))
          s = s.replace(re, "$1,$2");
        s = s.replace(/,(\d\d\d\d)$/, ".$1");
        return s.replace(/^\./, "0.")
      },
      MillenniumRepresentation(value){
           var reg=/(?=(?!)(\d{3})+$)/g;
           let StringValue = String(value).replace(reg, ',')
           if(StringValue.substring(0,1)===','){
               return StringValue.replace(/,/,'');
           }else{
               return StringValue
           }
      }


    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      },
      numString(val) {
        //强制保留4位小数
        let realVal = Number(val).toFixed(0);
        return realVal.toString();
      },
    }
  }
</script>

<style lang="less" scoped>
  .index {
    width: 100%;
    padding-top: 60px;
    padding: 60px 15px 10px;
    box-sizing: border-box;
    min-height: 100vh;
    background: #0C0D11 url(../assets/img/bg.png) no-repeat;
    background-position: center 80px;
    background-size: 100%;

    .indexdata {
      margin-top: 40px;
      width: 100%;
      font-size: 16px;
      padding: 5px 0;
      background: linear-gradient(90deg, #1C1C21, rgba(12, 13, 17, 0));
      text-align: center;
      border-radius: 10px;
      color: #fff;

      .flex {
        padding: 10px 0;
      }

      .tips {
        color: #A6AFB6;
        font-size: 12px;
      }
    }

    .info {
      color: #A6AFB6;
      font-size: 12px;
      line-height: 22px;
      padding: 10px;
      margin-top: 30px;
    }

    .speed {
      color: #A6AFB6;
      font-size: 15px;
      margin-top: 30px;

      .line_bg {
        margin-top: 15px;
        width: 100%;
        height: 20px;
        background: #202632;
        border-radius: 6px;
        position: relative;
        overflow: hidden;

        .line {
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          width: 0%;
          background-color: #FF7C08;
          border-radius: 6px;
          transition: all 0.5s ease-in;
          color: #fff;
          line-height: 20px;
          text-align: center;
          font-size: 12px;
        }
      }
    }

    .donation {
      width: 100%;
      margin-top: 30px;
      background: rgba(161, 157, 178, 0.2);
      color: #A6AFB6;
      font-size: 15px;
      border-radius: 6px;
      padding: 20px 15px;
      box-sizing: border-box;

      .input {
        height: 36px;

        input {
          flex: 1;
          width: 100px;
          height: 36px;
          background: #0C0D11;
          border-radius: 6px;
          outline: none;
          border: none;
          margin: 0 10px;
          color: #62606B;
          font-size: 16px;
          text-indent: 10px;
        }
      }
    }

    .carbon {
      color: #A6AFB6;
      font-size: 12px;
      line-height: 20px;
      font-family: Microsoft YaHei;
      margin-top: 30px;

      .list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .list_box {
          width: 48%;
          background-color: rgba(58, 64, 82, 0.5);
          border-radius: 8px;
          margin-bottom: 20px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          padding: 15px 10px;
          box-sizing: border-box;

          >img {
            height: 50px;
          }

          .title {
            font-size: 14px;
            font-weight: bold;
            color: #FF7C08;
            line-height: 30px;
          }

          .infor {
            font-size: 10px;
            line-height: 16px;
            color: #A6AFB6;
            height: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
          }
        }
      }
    }

    .invite_modal {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: 999;
      color: #fff;

      >div {
        width: 80%;
        background-color: rgba(75, 75, 75, 1);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #A6AFB6;
        font-size: 15px;
        border-radius: 6px;
        padding: 20px 15px;
        box-sizing: border-box;

        .input {
          height: 36px;

          input {
            flex: 1;
            width: 100px;
            height: 36px;
            background: #0C0D11;
            border-radius: 6px;
            outline: none;
            border: none;
            color: #62606B;
            font-size: 16px;
            text-indent: 10px;
          }
        }
      }
    }
  }
</style>



