import axios from 'axios'
import qs from 'querystring'
axios.defaults.timeout = 12000
// axios.defaults.baseURL = 'http://3.140.201.47'// untzhengshi
// axios.defaults.baseURL = 'http://www.xjkuang.com/'// shendunzhengshi
// axios.defaults.baseURL = 'http://192.168.1.190:10000'// test
// axios.defaults.withCredentials = true
// axios.defaults.crossDomain=true
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
export default {
  httpGet (url, params) {
    return new Promise((resolve, reject) => {
      axios.get(url, {params: params}).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // httpPost (url, params) {
  //   return new Promise((resolve, reject) => {
  //     axios.post(url, qs.stringify(params)).then(res => {
  //       resolve(res.data)
  //     }).catch(error => {
  //       reject(error)
  //     })
  //   })
  // },
  httpPost (url, params) {
    return new Promise((resolve, reject) => {
      axios.post(url, params).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  httpFormData (url, params) {  //formdata上传图片
    return new Promise((resolve, reject) => {
      axios.post(url, params).then(res => {
        resolve(res.data)
      }).catch(error => {
        reject(error)
      })
    })
  }
}



