<template>
  <div class="headertop">
    <div class="logo_box" @click="golink('/')">
      <img class="logo" src="../../assets/img/logo.png" alt="" />
      <span>CARBON LABS</span>
    </div>
    <span class="address">{{address | strlength}}</span>
    <div class="right" :class="{ activeicon: activeicon == true }" @click="activeiconfun()">
      <span></span>
    </div>
    <div class="mobile_box" :class="{ rightshow: activeicon == true }" @click.stop="activeiconfun()">
      <div class="mobile" @click.stop="">
        <span @click="switchLanguage('en')" v-if="lang == 'zh'">English</span>
        <span @click="switchLanguage('zh')" v-else>中文</span>
        <span v-for="(item, index) in navtext" :key="index" @click="golink(item.nav)">{{ item.title }}</span>
        <el-button class="button" type="primary" round @click="golink('/pledge/0')">Buy Now</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HeaderTop",
    data() {
      return {
        address: '',
        activeicon: false, //展示导航栏
        lang: 'zh',
        navtext: [{
            title: this.$t('lang.invit'),
            nav: "/invite"
          },
          {
            title: this.$t('lang.pool'),
            nav: "/ore_pool"
          },
          {
            title: this.$t('lang.account'),
            nav: "/account"
          }
        ],
      };
    },
    mounted: function() {
      console.log(ethereum.selectedAddress)
      this.lang = this.$i18n.locale
      this.connectWallet()
    },
    methods: {
      golink: function(link) {
        this.activeicon = false
        if ('' + this.$route.path == link) {
          return
        }

        // if (link == '/ore_pool') {
        //   this.$message({
        //     message: this.$t('lang.open'),
        //     type: 'warning'
        //   });
        //   return
        // }

        let _this = this
        _this.$router.push(link)
        // setTimeout(function() {
        //   _this.$router.push(link)
        // }, 300);
      },
      //链接钱包
      connectWallet: function() {
        ethereum
          .request({
            method: 'eth_requestAccounts'
          })
          .then(res => {
            this.address = res[0];
            if (this.$route.path == '/' || this.$route.path == '/index') {
              if (ethereum.selectedAddress) {
                // 获取邀请者
                this.$parent.getInvitors();
                // 授权额度查询
                this.$parent.allowance();
              }
            }
            if (this.$route.path.indexOf('/index') == 0) {
              if (ethereum.selectedAddress) {
                // 获取邀请者
                this.$parent.getInvitors();
                // 授权额度查询
                this.$parent.allowance();
              }
            }
          })
          .catch(error => {
            if (error.code === 4001) {
              console.log(error.message)
            } else {
              console.error(error)
            }
          })
      },
      //切换语言
      switchLanguage: function(type) {
        this.lang = type;
        this.$i18n.locale = type;

        this.navtext = [{
            title: this.$t('lang.invit'),
            nav: "/invite"
          },
          {
            title: this.$t('lang.pool'),
            nav: "/ore_pool"
          },
          {
            title: this.$t('lang.account'),
            nav: "/account"
          }
        ]

        if (this.$route.path == '/' || this.$route.path == '/index') {
          if (ethereum.selectedAddress) {

            // 授权额度查询
            this.$parent.allowance();
          }
        }
        this.activeicon = false

      },
      // 移动端右边按钮切换
      activeiconfun() {
        if (this.activeicon) {
          this.activeicon = false;
        } else {
          this.activeicon = true;
        }
      },

    },
    filters: {
      strlength(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(0, 7) + '***' + val.substring(leng - 4, leng)
        return val
      },
    }
  };
</script>

<style lang="less" scoped>
  .headertop {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #202632;
    position: fixed;
    top: 0;
    left: 0;
    transition: all 0.5s ease-in;
    z-index: 999;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
    font-size: 14px;

    .logo_box {
      display: flex;
      align-items: center;

      .logo {
        width: 40px;
        height: 40px;
      }
    }

    .address {
      flex: 1;
      text-align: right;
      padding-right: 20px;
    }

    .right {
      width: 30px;
      height: 30px;
      position: relative;

      span,
      span::before,
      span::after {
        position: absolute;
        left: 50%;
        height: 2px;
        content: "";
        transition: all 400ms ease;
        background: #fff;
        border-radius: 10px;

      }

      span {
        top: 50%;
        width: 16px;
        transform: translate(-50%, -50%);
      }

      span::before {
        top: -8px;
        width: 24px;
        left: 0;
      }

      span::after {
        top: 8px;
        width: 8px;
        left: 0;
      }
    }

    .activeicon {
      span {
        background: rgba(0, 0, 0, 0);
      }

      span:before {
        top: 0px;
        width: 24px;
        left: 50%;
        transform: translate(-50%) rotate(-45deg);
      }

      span:after {
        top: 0px;
        width: 24px;
        left: 50%;
        transform: translate(-50%) rotate(45deg);
      }
    }

    .mobile_box {
      position: fixed;
      width: 100%;
      top: 60px;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.9) url(../../assets/img/bg2.png) no-repeat;
      z-index: 99;
      background-position: center -30px;
      background-size: 100%;
      height: 0px;
      overflow: hidden;

      .mobile {
        height: 0px;
        overflow: hidden;
        position: absolute;
        top: 30px;
        left: 15px;
        right: 15px;
        transition: all 300ms ease;
        background: #1C1E24;
        display: flex;
        flex-direction: column;

        padding: 30px;
        box-sizing: border-box;
        line-height: 40px;
        border-radius: 6px;

        >span {
          background-color: #0C0D11;
          width: 100%;
          margin-bottom: 20px;
          text-align: center;
          font-size: 15px;
          font-family: SimHei;
        }

        .button {
          width: 50%;
          margin: 20px auto 0;
        }
      }

    }

    .rightshow {
      height: 100%;

      .mobile {
        height: 360px;
      }
    }
  }
</style>



