<template>
  <div class="ore_pool flex flex-direction">
    <HeaderTop></HeaderTop>
    <div class="banner">
      <el-carousel height="200px">
        <el-carousel-item v-for="(item,index) in list" :key="index">
          <div class="item" :style="{backgroundImage:'url('+item+')'}">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="flex_sub" style="background: rgba(255,255,255,0.9);">
      <div class="mining" v-for="(item,index) in order" :key="index" @click="golink(item.link)">
        <p class="tit">{{item.title}}</p>
        <p style="color: #A6AFB6;line-height: 30px;">{{item.info}}</p>
        <p class="tit">{{item.num}}</p>
        <p style="color: #202632;margin-bottom: 10px;">{{item.time}}</p>
        <span :class="{yjs:item.text == '已结束'}">{{item.text}}</span>
      </div>

    </div>

  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  export default {
    components: {
      HeaderTop
    },
    name: 'invite',
    data() {
      return {
        list: [
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg'
        ],
        order: [{
          title: '创世挖矿',
          info: '挖矿总数量（CBL）',
          num: '20000',
          time: '2021-06-11 20:00:00 ~ 2021-07-19 13:30:00',
          sjc:'1626672600000',
          text: '进行中',
          link: '/pool'
        }, {
          title: '流动性90天挖矿',
          info: '挖矿总数量（CBL）',
          num: '20000',
          time: '2021-06-11 20:00:00 ~ 2021-07-18 13:30:00',
          sjc:'1626586200000',
          text: '已结束',
          link: '/pool'
        }],
        time: '', //当前时间
        timer: null, //定时器
      }
    },

    mounted: function() {
      this.time = new Date().getTime(); //1603009495724,精确到毫秒
      console.log(this.time)
      this.addSetInterval()
    },
    beforeRouteLeave(to, form, next) {
      // 离开页面清空定时器
      clearInterval(this.timer)
      next()
    },
    methods: {
      golink: function(link) {
        this.$router.push(link)
      },
      // 添加定时器
      addSetInterval() {
        let _this = this
        this.timer = setInterval(() => {
          _this.time = _this.time + 1000
          // console.log(_this.order[0].sjc)


          for(let i =0;i<_this.order.length;i++){
            // console.log(i)
            if(_this.time>_this.order[i].sjc){
              _this.order[i].text = '已结束'
            }
          }
          // console.log(_this.time)
        }, 1000)
      }
    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      },

    }
  }
</script>

<style lang="less" scoped>
  .ore_pool {
    width: 100%;
    padding-top: 60px;
    padding: 60px 0px 0px;
    box-sizing: border-box;
    min-height: 100vh;
    background: #fff url(../assets/img/bg.png);
    background-position: center 180px;
    background-size: 100%;

    .banner {
      .item {
        height: 200px;
        background-size: 100%;
        background-position: center center;
      }
    }

    .mining {
      margin: 15px;
      background: rgba(255, 255, 255, 0.9);
      box-shadow: 0px 0px 17px 0px rgba(49, 49, 49, 0.15);
      border-radius: 8px;
      padding: 10px 15px;
      box-sizing: border-box;
      position: relative;
      overflow: hidden;

      .tit {
        font-size: 16px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: #212121;
        height: 30px;

      }

      >span {
        position: absolute;
        background-color: #FF7C08;
        color: #fff;
        top: 0;
        right: 0;
        width: 70px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        font-size: 13px;
      }

      .yjs {
        background-color: #dad7d7;
      }

    }

  }
</style>



