<style>
  * {
    margin: 0;
    padding: 0;
  }

  #app {
    font-family: FZDHTK, PingFang SC, 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
