    border-color: #FF7C08 !important;
    width: 100%;
    margin-top: 30px;
    font-size: 16px;
    letter-spacing: 2px;
  }

  #app .button2 {
    background-color: #F0EEEE !important;
    border-color: #F0EEEE !important;
    width: 100%;
    margin-top: 30px;
    font-size: 16px;
    letter-spacing: 2px;
    color: #8A909B;
  }

  #app .el-button--primary.is-disabled,
  #app .el-button--primary.is-disabled:active,
  #app .el-button--primary.is-disabled:focus,
  #app .el-button--primary.is-disabled:hover {
    color: #8A909B !important;
    background-color: #F0EEEE !important;
    border-color: #F0EEEE !important;
  }

