<template>
  <div class="ore_pool">
    <HeaderTop></HeaderTop>
    <div class="banner">
      <el-carousel height="200px">
        <el-carousel-item v-for="(item,index) in banner" :key="index">
          <div class="item" :style="{backgroundImage:'url('+item+')'}">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="mining">
      <p class="flex justify-between" style="height: 26px;">
        <span style="font-size: 16px;font-weight: 700;color: #212121;">创世挖矿</span>
        <span style="color: #A6AFB6;">进行中</span>
      </p>
      <p style="font-size: 11px;line-height: 24px;">活动时间：2021/06/11 20:00~ 2021/07/11 20:00</p>
      <div class="mining_info flex align-center">
        <div class="flex_sub">
          <p style="color: #A6AFB6;">挖矿总数量/CBL</p>
          <p class="num">{{reward | numString}}</p>
        </div>
        <div class="line"></div>
        <div class="flex_sub">
          <p style="color: #A6AFB6;">可以领取/CBL</p>
          <p class="num">{{allPending | numString}}</p>
        </div>
        <div class="line"></div>
        <div class="flex_sub">
          <p style="color: #A6AFB6;">质押总额/USD</p>
          <p class="num">{{tokenAmount}}</p>
        </div>
      </div>
      <div class="bottom">
        <el-button class="button" type="primary">全部领取</el-button>
        <span class="cbp">购买CBP</span>
      </div>
    </div>

    <div class="pool" v-for="(item,index) in list" :key="index">
      <p style="font-weight: 700;font-size: 16px;">{{item.poolName}}矿池</p>
      <p style="color: #A6AFB6;line-height: 28px;">质押{{item.poolName}}，获得CBL奖励</p>
      <img style="width: 70px;" src="../assets/img/CBL.png" alt="">
      <p>
        <el-button class="button" type="primary" @click="golink('/pledge/0')">立即挖矿</el-button>
      </p>
      <p class="flex justify-between" style="line-height: 26px;">
        <span style="color: #A6AFB6;">年化收益率：</span>
        <span>8.00%</span>
      </p>
      <p class="flex justify-between">
        <span style="color: #A6AFB6;">质押总量：</span>
        <span>{{item.amount}} {{item.poolName}}</span>
      </p>
    </div>

    <!-- <div class="pool">
      <p style="font-weight: 700;font-size: 16px;">UDDT矿池</p>
      <p style="color: #A6AFB6;line-height: 28px;">质押USDT，获得CBL奖励</p>
      <img style="width: 70px;" src="../assets/img/CBL.png" alt="">
      <p>
        <el-button class="button" type="primary" @click="golink('/pledge')">立即挖矿</el-button>
      </p>
      <p class="flex justify-between" style="line-height: 26px;">
        <span style="color: #A6AFB6;">年化收益率：</span>
        <span>8.00%</span>
      </p>
      <p class="flex justify-between">
        <span style="color: #A6AFB6;">质押总量：</span>
        <span>3333.9878USDT</span>
      </p>
    </div> -->




  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  import AnimatedNumber from "animated-number-vue";
  export default {
    components: {
      HeaderTop,
      AnimatedNumber
    },
    name: 'invite',
    data() {
      return {

        banner: [
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg',
          'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-71c61fc8-136b-41f6-9d2f-03d310fa8f0a/02924a78-cb25-4e71-8bbe-c35956f68b00.jpg'
        ],
        mingContract: new this.Web3.eth.Contract(this.$store.state.ming_abi, this.$store.state.ming_address),
        address: '',
        list: [],
        allPending: '0.00', //总共可领取
        tokenAmount: '0', //质押总额
        reward: '0'
      }
    },

    mounted: function() {
      console.log('mingContract', this.mingContract.methods)
      this.getAllPending();
      this.getData();
    },
    methods: {
      golink: function(link) {
        this.$router.push(link)
      },
      getData: function() {
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .poolLength()
          .call()
          .then(n => {
            console.log('矿池数量', n)

            for (let i = 0; i < n; i++) {
              this.mingContract.methods
                .poolInfo(i)
                .call()
                .then(res => {
                  //lpTokenAmount  此池中的lpToken存款金额，用于计算APY
                  res.lpTokenAmount = this.Web3.utils.fromWei(res.lpTokenAmount, 'ether')
                  // 总质押

                  let data = res

                  console.log('矿池信息', res)
                  this.mingContract.methods
                    .getUserInfo(i, this.address)
                    .call()
                    .then(user => {
                      //单个矿池质押数量
                      data.amount = this.Web3.utils.fromWei(user._amount, 'ether')
                      //所有矿池质押总量
                      this.tokenAmount = this.tokenAmount * 1 + data.amount * 1
                      //所有矿池质押收益
                      this.reward = this.reward * 1 + this.Web3.utils.fromWei(user._rewardDebt, 'ether') * 1
                      this.list.push(data)
                      console.log('user', user)


                    })

                })
            }

          })
      },
      // 总共可领取CBL
      getAllPending: function() {
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .getAllPendingAmount(this.address)
          .call()
          .then(res => {
            console.log('总共可领取', res)
            // this.allPending = res
            this.allPending = this.Web3.utils.fromWei(res, 'ether')

          })

      }
    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      },
      numString(val) {
        //强制保留4位小数
        let realVal = Number(val).toFixed(4);
        return realVal.toString();
      },

    }
  }
</script>

<style lang="less" scoped>
  .ore_pool {
    width: 100%;
    padding-top: 60px;
    padding: 60px 0px 10px;
    box-sizing: border-box;
    min-height: 100vh;
    background: #0C0D11 url(../assets/img/bg.png);
    background-position: center 180px;
    background-size: 100%;

    .banner {
      .item {
        height: 200px;
        background-size: 100%;
        background-position: center center;
      }
    }

    .mining {
      margin: 20px 15px;
      background: #FFFFFF;
      box-shadow: 0px 0px 17px 0px rgba(49, 49, 49, 0.15);
      border-radius: 8px;
      padding: 15px;
      box-sizing: border-box;

      .mining_info {
        text-align: center;
        height: 40px;
        margin-top: 6px;

        .line {
          width: 1px;
          height: 38px;
          background: #A6AFB6;
        }

        .num {
          font-weight: 700;
          font-size: 16px;
          margin-top: 6px;
        }
      }

      .bottom {
        position: relative;

        .button {
          width: 140px;
          margin: 20px auto 0;
          display: block;
        }

        .cbp {
          position: absolute;
          color: #FF7C08;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          font-size: 13px;
        }

        .cbp::after {
          position: absolute;
          left: 0;
          bottom: -4px;
          height: 2px;
          content: "";
          width: 100%;
          background: #FF7C08;

        }
      }

    }

    .pool {
      margin: 20px 15px;
      box-shadow: 0px 0px 17px 0px rgba(49, 49, 49, 0.15);
      border-radius: 8px;
      padding: 15px;
      box-sizing: border-box;
      background: rgba(161, 157, 178, 0.2);
      color: #fff;
      text-align: center;

      .button {
        width: 140px;
        display: block;
        margin: 10px auto;

      }

    }




  }
</style>



