<template>
  <div class="account">
    <HeaderTop></HeaderTop>
    <div class="title flex align-center">
      <img src="../assets/img/i8.png">
      <span>{{$t('lang.account')}}</span>
    </div>

    <div class="info">
      <div class="tit flex justify-between align-center">
        <div>{{$t('lang.a1')}}</div>
        <p v-clipboard:copy="address" v-clipboard:success="firstCopySuccess" v-clipboard:error="firstCopyError">
          <img src="../assets/img/i9.png" style="width: 40px;">
        </p>

      </div>
      <p style="margin-bottom: 30px;">{{address}}</p>

      <div class="tit flex justify-between align-center">
        <div>{{$t('lang.a2')}}</div>
        <p v-clipboard:copy="invite_address" v-clipboard:success="firstCopySuccess" v-clipboard:error="firstCopyError">
          <img src="../assets/img/i9.png" style="width: 40px;">
        </p>
      </div>
      <p style="margin-bottom: 30px;">{{invite_address}}</p>

      <div class="tit flex justify-between align-center">
        <div>{{$t('lang.a3')}}</div>
        <img src="../assets/img/i10.png" style="width: 40px;">
      </div>
      <p style="margin-bottom: 30px;color: #fff;font-size: 16px;">
        <animated-number :value="airdrop" :formatValue="formatToPrice" :duration="300" />
        <span>CBL</span>
      </p>

      <div class="tit flex">
        <div>{{$t('lang.a4')}}</div>

      </div>
      <p style="margin-bottom: 30px;color: #fff;font-size: 16px;position: relative;">
        <animated-number :value="available" :formatValue="formatToPrice" :duration="300" />
        <span>CBL</span>
        <el-button class="button" type="primary" round @click="claim()">{{$t('lang.a5')}}</el-button>
      </p>

      <div class="tit flex justify-between align-center">
        <div>{{$t('lang.a6')}}</div>
      </div>
      <div style="color: #fff;font-size: 16px;position: relative;">
        <p>
          <animated-number :value="balance" :formatValue="formatToPrice" :duration="300" />
          <span>CBL</span>
        </p>
        <span class="cbp">{{$t('lang.a7')}}</span>
      </div>

    </div>


  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  import AnimatedNumber from "animated-number-vue";
  export default {
    components: {
      HeaderTop,
      AnimatedNumber
    },
    name: 'account',
    data() {
      return {
        cblContract: new this.Web3.eth.Contract(this.$store.state.cbl_abi, this.$store.state.cbl_address),
        frozenContract: new this.Web3.eth.Contract(this.$store.state.frozen_abi, this.$store.state.frozen_address),
        address: '', //钱包地址
        invite_address: '', //邀请人地址
        balance: '', //cbl余额
        airdrop: '', //空投余额
        available: '', //空投可提取余额
      }
    },

    mounted: function() {
      console.log('frozenContract', this.frozenContract)
      this.getInvitors()
      this.getBalance()
    },
    methods: {
      //提取
      claim: function() {
        this.address = ethereum.selectedAddress
        if (this.available <= 0) {
          this.$message({
            message: this.$t('lang.a8'),
            type: 'warning'
          });
          return
        }
        this.frozenContract.methods
          .Claim()
          .send({
            from: this.address
          })
          .then(res => {
            console.log('提取', res)

          }).catch(error => {

            console.error(error)
          })
      },
      // 获取邀请者
      getInvitors: function() {
        this.address = ethereum.selectedAddress
        this.cblContract.methods
          .getInvitors(this.address, true)
          .call()
          .then(res => {
            this.invite_address = res[0]
            console.log('邀请者', res)

          })
      },
      //CBL余额
      getBalance: function() {
        this.address = ethereum.selectedAddress
        this.cblContract.methods
          .balanceOf(this.address)
          .call()
          .then(res => {
            this.balance = this.Web3.utils.fromWei(res, 'szabo')
            console.log('cblbalance', res)
          })

        //空投账户余额
        this.frozenContract.methods
          .getUserInfo(this.address)
          .call()
          .then(res => {
            // 空投余额  提现数量 最后提现区块
            let amount = res[0] - res[1] + ''
            this.airdrop = this.Web3.utils.fromWei(amount, 'szabo')
            console.log('空投余额', res[0] - res[1])
          })

        //空投可提取
        this.frozenContract.methods
          .getPendingAmount(this.address)
          .call()
          .then(res => {
            this.available = this.Web3.utils.fromWei(res, 'szabo')
            console.log('空投可提取', res)
          })
      },
      //数字转换为千分位
      formatToPrice(s) {
        s = s + ''
        s = s.replace(/^(\d*)$/, "$1.");
        s = (s + "0000").replace(/(\d*\.\d\d\d\d)\d*/, "$1");
        s = s.replace(".", ",");
        var re = /(\d)(\d{3},)/;
        while (re.test(s))
          s = s.replace(re, "$1,$2");
        s = s.replace(/,(\d\d\d\d)$/, ".$1");
        return s.replace(/^\./, "0.")
      },

      //复制
      firstCopySuccess(e) {
        console.log("copy arguments e:", e);
        this.$message({
          message: '复制成功',
          type: 'success'
        });
      },
      firstCopyError(e) {
        console.log("copy arguments e:", e);
        this.$message.error('复制失败');
      }
    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      }
    }
  }
</script>

<style lang="less" scoped>
  .account {
    width: 100%;
    padding-top: 60px;
    padding: 60px 15px 10px;
    box-sizing: border-box;
    min-height: 100vh;
    background: url(../assets/img/bg2.png) no-repeat;
    background-position: center top;
    background-size: 100%;

    .title {

      height: 80px;
      font-size: 16px;
      font-family: Microsoft YaHei;
      color: #A6AFB6;

      >img {
        width: 40px;
      }
    }

    .info {
      width: 100%;

      background: url(../assets/img/bg3.png) no-repeat;
      background-position: center top;
      background-size: 100% 100%;
      border-radius: 14px;
      color: #A6AFB6;
      padding: 15px;
      box-sizing: border-box;
      font-size: 13px;

      .tit {
        width: 100%;
        height: 40px;
        position: relative;

        >div {
          background: url(../assets/img/bg4.png) no-repeat;
          background-position: center 18px;
          background-size: 100%;
          height: 30px;
          line-height: 30px;
        }

      }

      .button {
        width: 100px !important;
        padding: 10px;
        position: absolute;
        bottom: 2px;
        right: 0;
        margin: 0;
      }

      .cbp {
        position: absolute;
        color: #fff;
        right: 0;
        top: -4px;
        font-size: 14px;
      }

      .cbp::after {
        position: absolute;
        left: 0;
        bottom: -4px;
        height: 2px;
        content: "";
        width: 100%;
        background: #fff;
      }
    }

  }
</style>



