<template>
  <div class="invite">
    <HeaderTop></HeaderTop>
    <div style="background-color: rgba(255,255,255,0.7);">
      <div class="invitedata">
        <div class="flex align-center">
          <img style="width: 36px;margin-top: 4px;" src="../assets/img/i7.png" alt="">
          <span style="font-size: 20px;">{{$t('lang.invit')}}</span>
        </div>
        <p style="font-size: 15px;line-height: 30px;">{{$t('lang.i1')}}</p>
        <p style="color: #FFDFC3;line-height: 20px;">{{$t('lang.i2')}}</p>
      </div>
    </div>

    <div class="bg">
      <div class="invite_link">
        <p style="color: #B5B5B5;font-size: 14px;">{{$t('lang.i3')}}</p>
        <p style="color: #212121;padding: 20px 0;word-wrap : break-word;">{{url}}</p>
        <div>
          <el-button class="button" type="primary" v-clipboard:copy="url" v-clipboard:success="firstCopySuccess"
            v-clipboard:error="firstCopyError">{{$t('lang.i4')}}</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  export default {
    components: {
      HeaderTop
    },
    name: 'invite',
    data() {
      return {
        url: ''
      }
    },

    mounted: function() {
      this.url = 'https://static-71c61fc8-136b-41f6-9d2f-03d310fa8f0a.bspapp.com/#/index/' + ethereum.selectedAddress
    },
    methods: {
      firstCopySuccess(e) {
        console.log("copy arguments e:", e);
        this.$message({
          message: this.$t('lang.i5'),
          type: 'success'
        });
      },
      firstCopyError(e) {
        console.log("copy arguments e:", e);
        this.$message.error(this.$t('lang.i6'));
      }
    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      },

    }
  }
</script>

<style lang="less" scoped>
  .invite {
    width: 100%;
    padding-top: 60px;
    padding: 60px 0px 10px;
    box-sizing: border-box;
    min-height: 100vh;
    background: #fff url(../assets/img/bg.png) no-repeat;
    background-position: center 90px;
    background-size: 100%;
    display: flex;
    flex-direction: column;

    .invitedata {
      padding: 20px 20px 50px;
      width: 100%;
      background-color: rgba(255, 129, 15, 0.9);
      box-sizing: border-box;
      color: #fff;

    }

    .bg {
      flex: 1;
      background-color: rgba(255, 255, 255, 0.9);

      .invite_link {
        position: relative;
        margin: -30px 15px 0;
        // height: 200px;
        background: #FFFFFF;
        box-shadow: 0px 0px 17px 0px rgba(49, 49, 49, 0.2);
        border-radius: 6px;
        padding: 20px 15px 30px;

      }
    }
  }
</style>



