<template>
  <div class="pledge flex flex-direction">
    <HeaderTop></HeaderTop>
    <div class="title">
      <span>质押</span>
    </div>
    <div class="bg flex_sub">
      <div class="pledge_box" style="margin-top: -100px;">
        <div class="top flex align-center justify-between">
          <img src="../assets/img/CBL.png" style="width: 46px;">
          <div style="width: 120px;">
            <el-button class="button" type="primary" round :disabled="disabled" :loading="claimall_loading" @click="claimAll()">全部提取</el-button>
          </div>
        </div>
        <p style="color: #202632;font-size: 16px;margin-top: 15px;">CBL 收益</p>
        <p style="font-size: 26px;color: #FF7C08;margin-top: 15px;margin-bottom: 10px;">
          <animated-number :value="pending" :duration="300" />

        </p>
        <span class="cbp">将CBL添加到钱包</span>
      </div>
      <div class="pledge_box" style="margin-top: 15px;">
        <div class="top flex align-center justify-between">
          <div class="flex align-center">
            <img src="../assets/img/CBL.png" style="width: 46px;z-index: 100;">
            <img src="../assets/img/BTC.png" style="width: 46px;margin-left: -22px;">
          </div>
          <div style="width: 120px;" v-if="allowancebalance<=0">
            <el-button class="button" type="primary" round @click="approve()" :loading="approve_loading">授权</el-button>
          </div>
          <div class="all flex align-center justify-between" v-else style="width: 120px;">
            <span class="flex_sub el-icon-minus" @click="showModal('extract')"></span>
            <p class="line"></p>
            <span class="flex_sub el-icon-plus" @click="showModal('pledge')"></span>
          </div>
        </div>
        <p style="color: #202632;font-size: 16px;margin-top: 15px;">CBL/USDT USDT挖矿</p>
        <p style="font-size: 26px;color: #FF7C08;margin-top: 15px;">{{amount}}</p>
      </div>

      <div class="all flex align-center justify-between" style="margin-top: 20px;">
        <span class="flex_sub">全部领取</span>
        <p class="line"></p>
        <span class="flex_sub">添加流动性</span>
      </div>
      <div class="info">提取收益将扣除10%手续费，质押物100%退还</div>
    </div>


    <!-- 提取 -->
    <div class="invite_modal" v-if="extract_modal">
      <div>
        <p style="text-align: center;height: 40px;font-size: 16px;">提取 USDT</p>
        <div class="input flex justify-between align-center">
          <input type="text" v-model="extract_num" placeholder="请输入提取数量">
          <div class="allbut" @click="extract_num = pending">全部</div>
        </div>
        <p style="text-align: right;font-size: 14px;margin-top: 10px;">{{pending}}USDT 可提取余额</p>

        <div class="flex align-center">
          <el-button class="button2" type="primary" @click="hideModal()">取消</el-button>
          <el-button class="button" type="primary" :loading="extract_loading" @click="claim()">确认</el-button>
        </div>
      </div>
    </div>

    <!-- 质押 -->
    <div class="invite_modal" v-if="pledge_modal">
      <div>
        <p style="text-align: center;height: 40px;font-size: 16px;">质押 USDT</p>
        <div class="input flex justify-between align-center">
          <input type="text" v-model="pledge_num" placeholder="请输入质押数量">
          <div class="allbut" @click="pledge_num = usdtbalance">全部</div>
        </div>
        <p style="text-align: right;font-size: 14px;margin-top: 10px;">{{usdtbalance}} USDT余额</p>

        <div class="flex align-center">
          <el-button class="button2" type="primary" @click="hideModal()">取消</el-button>
          <el-button class="button" type="primary" :loading="pledge_loading" @click="pledge()">确认</el-button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import HeaderTop from './common/HeaderTop'
  import AnimatedNumber from "animated-number-vue";
  export default {
    components: {
      HeaderTop,
      AnimatedNumber
    },
    name: 'pledge',
    data() {
      return {
        usdtContract: new this.Web3.eth.Contract(this.$store.state.usdt_abi, this.$store.state.usdt_address),
        cblContract: new this.Web3.eth.Contract(this.$store.state.cbl_abi, this.$store.state.cbl_address),
        mingContract: new this.Web3.eth.Contract(this.$store.state.ming_abi, this.$store.state.ming_address),
        address: '',
        pledge_num: '',//质押数量
        extract_num:'',//提取数量
        extract_modal: false,//提取弹窗
        pledge_modal:false,//质押弹窗
        pledge_loading: false,
        extract_loading:false,
        approve_loading:false,
        claimall_loading:false,
        id:'',
        usdtbalance:'',
        allowancebalance:'',//授权额度
        pending:'0',//可领取收益
        amount:'0',//已质押数量
        disabled:true,
      }
    },

    mounted: function() {
      this.id = this.$route.params.id;
      this.allowance();
      this.getProfit();
      console.log('mingContract',this.mingContract)

    },
    methods: {
      showModal: function(text) {
        //点击加号质押
        if(text == 'pledge'){
          this.pledge_modal = true
        } else if(text == 'extract'){
          //点击减号提取
          this.extract_modal = true
        }
      },
      hideModal:function(){
        this.pledge_modal = false
        this.extract_modal = false
      },
      //授权
      approve: function() {
        this.approve_loading = true
        this.address = ethereum.selectedAddress
        this.usdtContract.methods
          .approve(this.$store.state.ming_address, this.Web3.utils.toWei('100', 'ether'))
          .send({
            from: this.address
          })
          .then(res => {

            this.allowance()
            this.$message({
              message: this.$t('lang.place5'),
              type: 'success'
            });
          }).catch(error => {
            this.approve_loading = false
            if (error.code === 4001) {
              // this.invite_modal = false
            } else {
              console.error(error)
            }
          })
      },
      // 授权额度查询
      allowance: function() {
        this.address = ethereum.selectedAddress
        this.usdtContract.methods
          .allowance(this.address, this.$store.state.ming_address)
          .call()
          .then(res => {
            this.approve_loading = false
            this.allowancebalance = this.Web3.utils.fromWei(res, 'ether');
            if (res > 0) {

            } else {

            }
            console.log('授权额度', this.Web3.utils.fromWei(res, 'ether'))
          })
        //获取usdt余额
        this.usdtContract.methods
          .balanceOf(this.address)
          .call()
          .then(res => {
            this.usdtbalance = this.Web3.utils.fromWei(res, 'ether')
            console.log('usdt余额', this.usdtbalance)
          })
      },
      // 质押
      pledge:function(){
        if(!this.pledge_num){
          this.$message({
            message: '请输入质押数量',
            type: 'warning'
          });
          return
        }
        if(this.pledge_num>this.usdtbalance){
          this.$message({
            message: '余额不足',
            type: 'warning'
          });
          return
        }
        this.pledge_loading = true
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .deposit(this.id, this.Web3.utils.toWei(this.pledge_num, 'ether'))
          .send({
            from: this.address
          })
          .then(res => {
            this.$message({
              message: '质押成功',
              type: 'success'
            });
            this.pledge_loading = false
            this.pledge_modal = false
            this.allowance();
            this.getProfit();

          }).catch(error => {
            this.pledge_loading = false
            this.pledge_modal = false
            if (error.code === 4001) {
              // this.invite_modal = false
            } else {
              console.error(error)
            }
          })
      },
      //获取收益
      getProfit:function(){
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .getPendingAmount(this.id,this.address)
          .call()
          .then(res => {
            console.log('总共可领取', res)
            this.pending = this.Web3.utils.fromWei(res, 'ether')
            if(res>0){
              this.disabled = false
            } else {
              this.disabled = true
            }

          })

        this.mingContract.methods
          .getUserInfo(this.id, this.address)
          .call()
          .then(res => {
            console.log('userinfo',res)
            // this.pending = this.Web3.utils.fromWei(res._rewardToClaim, 'ether')
            this.amount = this.Web3.utils.fromWei(res._amount, 'ether')
          })





      },
      //全部提取
      claimAll:function(){
        this.claimall_loading = true
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .claimAll()
          .send({
            from: this.address
          })
          .then(res => {
            this.$message({
              message: '提取成功',
              type: 'success'
            });
            this.disabled = true
            this.claimall_loading = false

          }).catch(error => {
            this.claimall_loading = false
            if (error.code === 4001) {
              // this.invite_modal = false
            } else {
              console.error(error)
            }
          })
      },
      //提取
      claim:function(){
        if(!this.extract_num){
          this.$message({
            message: '请输入提取数量',
            type: 'warning'
          });
          return
        }
        if(this.extract_num>this.pending){
          this.$message({
            message: '余额不足',
            type: 'warning'
          });
          return
        }
        this.extract_loading = true
        this.address = ethereum.selectedAddress
        this.mingContract.methods
          .claim(this.id)
          .send({
            from: this.address
          })
          .then(res => {
            this.$message({
              message: '提取成功',
              type: 'success'
            });
            this.extract_loading = false

          }).catch(error => {
            this.extract_loading = false
            if (error.code === 4001) {
              // this.invite_modal = false
            } else {
              console.error(error)
            }
          })
      }



      //
    },
    filters: {
      strleft(val) {
        val = '' + val;
        val = val.substring(0, 10) + '...'
        return val
      },
      strright(val) {
        val = '' + val;
        var leng = val.length;
        val = val.substring(leng - 6, leng)
        return val
      }
    }
  }
</script>

<style lang="less" scoped>
  .pledge {
    width: 100%;
    padding-top: 60px;
    padding: 60px 0px 0px;
    box-sizing: border-box;
    min-height: 100vh;
    background: #fff url(../assets/img/bg.png) no-repeat;
    background-position: center 200px;
    background-size: 100%;

    .title {
      height: 160px;
      font-size: 18px;
      font-family: Microsoft YaHei;
      color: #A6AFB6;
      line-height: 60px;
      background: #0C0D11 url(../assets/img/bg2.png) no-repeat;
      background-position: center -40px;
      background-size: 100%;
      padding: 0 30px;
    }

    .bg {
      width: 100%;
      background-color: rgba(255, 255, 255, 0.9);
      padding: 0 15px;
      box-sizing: border-box;

      .pledge_box {
        width: 100%;
        background: #FFFFFF;
        box-shadow: 0px 0px 17px 0px rgba(49, 49, 49, 0.15);
        border-radius: 10px;
        padding: 20px;
        box-sizing: border-box;

        .top {
          height: 44px;

          .button {
            margin: 0 !important;
          }
        }

        .cbp {
          position: relative;
          font-size: 14px;
          color: #FF7C08;

        }

        .cbp::after {
          position: absolute;
          left: 0;
          bottom: -4px;
          height: 2px;
          content: "";
          width: 100%;
          background: #FF7C08;
        }
      }

      .all {
        width: 100%;
        height: 44px;
        background: #FF7C08;
        border-radius: 20px;
        text-align: center;
        font-size: 16px;
        color: #FFFFFF;
        line-height: 44px;

        .line {
          height: 24px;
          width: 1px;
          background-color: #fff;
        }
      }

      .info {
        width: 100%;
        height: 28px;
        box-shadow: 0px 0px 9px 0px rgba(49, 49, 49, 0.08);
        font-size: 12px;
        font-family: SimHei;
        margin-top: 20px;
        color: #464B55;
        line-height: 28px;
        text-align: center;
      }
    }
  }

  .invite_modal {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;

    >div {
      width: 90%;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 15px;
      border-radius: 6px;
      padding: 20px 15px;
      box-sizing: border-box;

      .input {
        height: 50px;
        background-color: #F0EEEE;
        padding: 0 10px;
        border-radius: 30px;

        .allbut {
          width: 90px;
          height: 36px;
          background: linear-gradient(0deg, #FF7C08, #e6a23c);
          color: #fff;
          text-align: center;
          line-height: 36px;
          border-radius: 20px;
        }

        input {
          flex: 1;
          width: 100px;
          height: 36px;
          background: rgba(0, 0, 0, 0);
          border-radius: 6px;
          outline: none;
          border: none;
          color: #62606B;
          font-size: 16px;
          text-indent: 10px;
        }
      }
    }
  }
</style>



