import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import invite from '@/components/invite' //邀请
import ore_pool from '@/components/ore_pool' //社区矿池
import pool from '@/components/pool' //挖矿
import account from '@/components/account' //我的账户
import pledge from '@/components/pledge' //质押



Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: index,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/index/:share',
      name: 'index',
      component: index,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/invite',
      name: 'invite',
      component: invite,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/ore_pool',
      name: 'ore_pool',
      component: ore_pool,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/pool',
      name: 'pool',
      component: pool,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/account',
      name: 'account',
      component: account,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    {
      path: '/pledge/:id',
      name: 'pledge',
      component: pledge,
      meta: {
        keepAlive: true, //此组件不需要被缓存
        isBack: false, //用于判断上一个页面是哪个
      }
    },
    
    

  ]
})



